import { ExamResultsDetails } from "../interfaces/exam";
import { PositionAPICourse } from "../interfaces/position";
import { questionType, questionTypeElements, questionTypeElementsType } from "../interfaces/question";

export const getQuestionType = (question_types: questionType[] | null, type_id: string) => {
  let type: questionTypeElementsType | null = null;
  if (question_types !== null) {
    let check = question_types.find(itm => itm.id === type_id);
    if (check !== undefined) {
      let check_type = questionTypeElements.find(itm => check && itm.key === check.element);
      if (check_type !== undefined) {
        type = check_type.key
      }
    }
  }
  return type;
}

export const findTotalMarks = (courses: PositionAPICourse[], results: ExamResultsDetails[] | null) => {
  if (results !== null) {
    let sum: number = 0;
    for (let course of courses) {
      let test = results.find(itm => itm.course === course.course_id);
      if (test !== undefined) {
        sum = sum + test.user_total_marks;
      }
    }
    return sum.toFixed(2)
  } else {
    return "Wait..."
  }
}

export const CheckIfExamDone = (courses: PositionAPICourse[], results: ExamResultsDetails[] | null) => {
  let res: boolean = false;
  if (results !== null) {
    for (let course of courses) {
      let test = results.find(itm => itm.course === course.course_id);
      if (test !== undefined) {
        res = true;
      }
    }
  }
  return res;
}